<template>
    <div class="container">
         <homeHeader></homeHeader><!--  展示引入的header组件 -->
         <div class="content">
             <ul class="cont-ul">
                <!-- list组件展示区，并用v-for来将数据遍历，:xx="xxx" 是用来给子组件传递数据的 -->
                <list v-for="item in items" :price="item.price" :img="item.img" :title="item.title" :key="item.id"></list>

             </ul>
         </div>
         <commonFooter></commonFooter><!--  展示引入的footer组件 -->
         
    </div>
</template>

<script>
    import homeHeader from '../components/homeHeader'
    import commonFooter from '../components/commonFooter'
    import List from '../components/list'

    export default {
        data(){
            return {
                items:[] //定义一个新数组
            }  
        },
        components:{
            homeHeader,
            commonFooter,
            List
        },
        created (){  //这个是vue的钩子函数，当new Vue()实例创建完毕后执行的函数 */
            this.$http.get('/api/goods').then((res)=>{
                this.items=res.data.data;
            });
        }
    }
</script>

<style scoped>
.container {
    width: 100%;
    margin: 0 auto;
  }
  .content {
    margin-bottom: 1.8rem;
  }
  .cont-ul {
    padding-top: 0.5rem;
    background-color: #ccc;
  }
  .cont-ul::after {
    content: '';
    display: block;
    clear: both;
    width: 0;
    height: 0;
  }

</style>


